<template>
 <div class="center">
    <div class="center_breadcrumb">
         <Crumb @headInfo="headInfo"/>
    </div>
    <div class="center_content">
        <div class="center_content_search" style="display: none">
            <div class="center_content_search_left">
                <div class="search-input-suffix gap">
                    <label>培养方案版本：</label>
                    <el-select v-model="versionId" placeholder="请选择">
                        <el-option
                        v-for="item in versionOptions"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                        </el-option>
                    </el-select>
                </div>
            </div>
        </div>
         <div class="table-container">
             <el-table
                :data="tableData"
                stripe
                class="educe-table"
                style="width: 100%">
                <el-table-column
                prop="name"
                label="材料名称">
                </el-table-column>
                <el-table-column
                prop="claim"
                label="对应标准要求">
                </el-table-column>
                <el-table-column
                label="操作"
                width="120">
                <template slot-scope="scope">
                    <div class="operate-button distance">
                         <SvgIcon style="color: #03b187" icon-class="iconxuexizhuangtaixiangqing"></SvgIcon>
                        <el-button
                        @click.native.prevent="educeRow(scope.row.id)"
                        type="text"
                        size="small">
                        导出
                        </el-button>
                    </div>                   
                </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
 </div>
</template>

<script>
import trainingProgramApi from '@/api/trainingProgramApi';
export default {
 name: 'BackingMaterial',
 data() {
 return {
    pageTitle:'支撑材料导出',
    versionOptions:[], 
    versionId:'',
    tableData:[
        {
            name:'本专业培养目标.doc',
            claim:'标准要求：有公开的、符合学校定位的、适应社会经济发展需要的培养目标。',
            id:'0001'
        },
        {
            name:'培养目标修改内容对比.doc',
            claim:'标准要求：定期评价培养目标的合理性，并根据评价结果对培养目标进行修订。评价与修订过程有行业或企业专家参与。。',
            id:'0002'
        },
        {
            name:'本专业毕业要求及其对通用标准要求的支撑.doc',
            claim:'本专业对毕业生有明确的、公开的毕业要求，该毕业要求与中国工程教育认证通用标准一致。',
            id:'0003'
        },
        {
            name:'本专业毕业要求对培养目标的支撑.doc',
            claim:'本专业对毕业生有明确的、公开的毕业要求，能够充分支撑本专业毕业生培养目标的达成。',
            id:'0004'
        },
        {
            name:'课程对毕业要求支撑矩阵 .doc',
            claim:'',
            id:'0005'
        },
        {
            name:'课程目标、评价方法以及与毕业要求的支撑关系.doc',
            claim:'',
            id:'0006'
        },
    ]
 }
 },
 created(){},
 mounted(){
  this.getList()
 },
 methods:{
     getList() {
        if(localStorage.getItem('versionId')) {
          this.versionId = localStorage.getItem('versionId')
          // this.initData()
        } else {
          setTimeout(() => {
            this.getList()
          }, 200);
        }
      },
    //  versionAll(){
    //      const url =trainingProgramApi.versionAll;
    //      this.$get(url).then(res =>{
    //          if(res.status === 1){               
    //             this.versionOptions=res.data;
    //             this.versionId=res.data[0].id
    //          }else{
    //            this.$notify.error({
    //             title: 'error',
    //             duration:1500,
    //             message: res.message
    //             })
    //          }
    //      }).catch(err =>{
    //         console.log(err)
    //      })
    //  },
     educeRow(id){
       const self=this
       let url=''
       switch(id) {
        case '0001':
            break;
        case '0002':
            break;
         case '0003':
            break;
        case '0004':
            url=trainingProgramApi.goalEduce({
                baseUrl:self.API_URL,
                versionID:self.versionId
            })
            break;
         case '0005':
            url=trainingProgramApi.courseEduce({
                baseUrl:self.API_URL,
                versionID:self.versionId
            })
            break;
        case '0006':
            break;
        default:
        console.log('err')
         break;
       }      
       if(url){
           window.location.href= url
       }else{
           self.$notify.info({
            title: '提示',
            duration:1500,
            message: '暂无导出'
            });
       }

     },
     headInfo(item){
        this.pageTitle=item[1]
     }
 }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss';
  .educe-table{
      tbody{
          tr{
              td:first-child{
                  color:#3366ff;
              }
          }
      }
  }
</style>



